.affordable-pricing{
    position: relative;
    margin-top: 100px;
    height: 100vh;
    z-index: 200;
}
.affordable-pricing .title{
    position: relative;
}
.affordable-pricing .iconDp{
  position: absolute;
  left: 30%;
  top: 53px;
}
.affordable-pricing .imageBill{ 
    position: relative;
    z-index: 200;
    padding-top: 34px;
}

.affordable-pricing .wrapp{
    display: flex;
    margin: 0 0 16px 0;
}
.affordable-pricing .wrapp svg{
    margin-right: 16px;
}
.affordable-pricing .button {
    width: 100%;
    height: 64px;
    border: 1px solid #ADADAD;
    box-sizing: border-box;
    border-radius: 16px;
    background: #fff;
    color: #6C5CE7;
    font-weight: bold;
    font-size: 18px;
    line-height: 32px;
    letter-spacing: 0.2px;
    max-width: 306px;
    margin-top: 136px;
}
.affordable-pricing .cards{
    padding-top: 40px;
}
.affordable-pricing .card{
 width: 370px;
 height: 536px;
 padding: 32px;
 border: none;
 border-radius: 16px;
 filter: drop-shadow(0px 8px 8px rgba(38, 50, 56, 0.12)) drop-shadow(0px 16px 24px rgba(38, 50, 56, 0.08));
}
.affordable-pricin .card-title{
 padding-top: 16px;
}

.card .icon-free{
    margin:  16px 0 24px 0;
}
